import * as actions from '../types/action.types'
import { createAction } from '@reduxjs/toolkit'
export const mapStateToProps = state => {
    return {
        // customerInfo: state.pizzaReducer.customerInfo,
        pizzaRecipes: state.pizzaRecipes,
        router: state.router
    }
}

export const mapDispatchToProps = dispatch => {
    return {
               addTopping: (toppingKey, pizzaKey)  => dispatch({ type: actions.ADD_TOPPING, toppingKey: toppingKey, pizzaKey: pizzaKey }),
              createOrder: order    => dispatch({ type: actions.CREATE_ORDER, order: order }),
        fetchPizzaRecipes: ()       => dispatch({ type: actions.FETCH_PIZZA_RECIPES }),

    }
}



export const addToppingAction = createAction(actions.ADD_TOPPING)
export const createOrderAction = createAction(actions.CREATE_ORDER)
export const fetchPizzaRecipesAction = createAction(actions.FETCH_PIZZA_RECIPES)
export const setPizzaRecipesAction = createAction(actions.SET_PIZZA_RECIPES)
export const setPizzaRecipesErrorAction = createAction(actions.SET_PIZZA_RECIPES_ERROR)
export const setSelectedPizzaAction = createAction(actions.SET_SELECTED_PIZZA)